import React from 'react'

export default function ProjectsNavigation({projectsAmount, current}) {
    return (
        <div className="projects-navigation">
            {
                [...Array(projectsAmount)].map((x, i) => (
                    <div className= {"navigation-dot " + (current == i ? "selected-dot" : "normal-dot")} />
                ))
            }
        </div>
    )
}
