import './Overtime.scss';
import React, { Component } from 'react'

export default class Overtime extends Component {
    constructor(props) {
        super(props);
        this.state = {
            freeCodeCampScore : 0,
        };
    }

    componentDidMount() {
        let freecodecampRequest = fetch('https://api.freecodecamp.org/api/users/get-public-profile?username=sdx95', 
        {method:"GET"}).then(res => res.json()).then(res => this.setState({freeCodeCampScore : res.entities.user.sdx95.points}));
    }

    render() {
        return (
            <div className="container-base overtime">
            <div className="text--category">Free time</div>
            <div className="overtime-website-container">
                <img className="website-icon" src="Icons/codewars.png" alt=""/>
                <img src="https://www.codewars.com/users/Sudix95/badges/large" alt=""/>
            </div>
            <div className="overtime-website-container">
                <img className="website-icon" src="Icons/freecodecamp.png" alt=""/>
                <div className="website-score">SCORE: {this.state.freeCodeCampScore}</div>
            </div>
        </div>
        )
    }
}
