import React from 'react'
import { ExternalLinks } from './Data/ExternalLinks';
import { RefElements } from './Data/RefElements';
import './Navbar.scss';

export default function Navbar() {
    return (
        <div className="Navbar container-base">
            <div className="logo">Portfolio</div>
            <div className="navigation">
                <button className="button-fixedsize button--selected" onClick={() => RefElements.experienceRef.current.scrollIntoView({ behavior: "smooth" })}>Experience</button>
                <button className="button-fixedsize button--bordered" onClick={() => window.open(ExternalLinks.linkedin)}>Hire me</button>
            </div>
        </div>
    )
}
