import React, { Component, useRef, useState, useEffect } from 'react'
import ExperienceJobTitle from './ExperienceJobTitle';
import JobProjectCard from './JobProjectCard';
import { experienceData } from "../Data/ExperienceData";
import { projects } from "../Data/ProjectsData";
import { jobSkillData } from "../Data/JobSkillsData";
import JobSkills from './JobSkills';
import { RefElements } from 'Components/Data/RefElements';
import ProjectsNavigation from './ProjectsNavigation';

const JobExperienceModel = [
    {
        job: experienceData[0],
        leftContainer: "white-container",
        rightContainer: "black-container",
        skills: jobSkillData.unitFour,
        projects: [projects.erp, projects.agresso]
    },
    {
        job: experienceData[1],
        leftContainer: "black-container",
        rightContainer: "white-container",
        skills: jobSkillData.picadilla,
        projects: [projects.nrc2, projects.nrc, projects.gts]
    }
];

function Job({ job }) {
    const [slide, setSlide] = useState(0);

    function nextValue(i, max) {
        if(i + 1 === max)
            return 0;
        return i + 1;
    }

    function nextSlide() {
        let nextID = nextValue(slide, job.projects.length);
        console.log(nextID + " " + job.projects.length);
        setSlide(nextID);
    }

    useEffect(() => {
        const nextSlideInterval = setInterval(() => {
            nextSlide();
        }, 4000);
        return () => {
            clearInterval(nextSlideInterval);
        }
    }, [slide])


    return (
        <div className="job inline-container">
            <div className={"half-container job-description " + job.leftContainer}>
                <ExperienceJobTitle experience={job.job} />
                <JobSkills job={job.skills} />
            </div>
            <div className={"half-container " + job.rightContainer}>
                <div className="job-projects-container" onClick={() => nextSlide()}>
                    {
                        job.projects.map((project, index) => (<JobProjectCard project={project} isCurrent={slide === index} />))
                    }
                </div>
                <ProjectsNavigation projectsAmount={job.projects.length} current={slide}></ProjectsNavigation>
            </div>
        </div>
    )
}

export default function Experience() {
    RefElements.experienceRef = useRef(null);
    return (
        <div>
            <div className="container-base">
                <div ref={RefElements.experienceRef} className="text--category">Experience</div>
                <Job job={JobExperienceModel[0]} />
                <Job job={JobExperienceModel[1]} />
            </div>
            <script src="">new Carousel();</script>
        </div>
    )
}