import React from 'react'
import './Intro.scss';
import {ExternalLinks} from '../Data/ExternalLinks';

export default function Intro() {
    return (
        <div className="Intro container-base">
            <div className="intro--leftside">
                <div className="intro--welcome text--normal">Hi I'm</div>
                <div className="intro--name bold">Piotr Sudo≈Ç</div>
                <div className="intro--description text--normal">Full Stack Developer .Net/React</div>
                <button className="linked-in" onClick={() => window.open(ExternalLinks.linkedin)}><img className="linked-in-icon" src={"./Icons/linkedin.png"}/> </button>
            </div>
            <div className="intro--rightside">
                <img className="intro--picture" src="./me2.png"/>
            </div>
        </div>
    )
}
