import React, { Component } from 'react'
import './Experience.scss';

export default class JobSkills extends Component {
    render() {
        var array =  this.props.job;
        return (
            <div className="job-skill-container">
                {
                    array.map((element, index) => 
                        (
                            <div className="job-skill-text-container">
                                <div className="job-skill-text-point"># </div> 
                                <div className="job-skill-text">{element}</div>
                            </div>
                        )
                    )
                }
            </div>
        )
    }
}
