import Icon from 'Components/Common/Icon';
import React from 'react'
import './Experience.scss';
import {StackIconHandler} from '../Common/StackIconHandler';

export default function JobProjectCard(props) {
    var project = props.project;
    var techStack = project.techStack;
    var {isCurrent} = props;
    return (
        <div className={"job-project-card " + (isCurrent ? "card-active" : "card-inactive")}>
            <div className="project-logo-container">
                <img className="project-logo" src={"./Icons/" + project.logo} alt={project.name}/>
                <div className="project-title text--center">{project.type}</div>
            </div>
            <div className="project-tech-stack">
            {
              CreateTechStackIcon(techStack)
            }
            </div>
        </div>
    )
}
function CreateTechStackIcon(techStack) {
    return techStack.map((tech, index) => (
        <div className="tech-stack-icon-container">
            <Icon className="tech-stack-icon" width="100%" height="100%" iconName={StackIconHandler.GetIcon(tech)} />
        </div>
    ));
}

