import React from 'react';
import './Skills.scss';
import { SkillsData } from "../Data/SkillsData.js";
import {StackIconHandler} from "../Common/StackIconHandler"

export default function Technologies() {
    return (
        <div className="container-base skills-component">
            <div className="text--center text--category">Technologies</div>
            <div className="main-skill-container">
            {
                SkillsData.main.map((skill, index) => (<MainSkill className="skill" skill={skill}></MainSkill> ))
            }
            </div>
            <div className="text--center"></div>
            {
                SkillsData.other.map((skill, index) => (<OtherSkill className="skill-minor" name={skill}></OtherSkill> ))
            }
        </div>
    )
}

export function MainSkill({skill, className}) {
    const {name, years, level} = skill;
    const maxLevel = 6;
    const skillLevelDots = [];
    for (let index = 0; index < maxLevel; index++) {
        skillLevelDots.push(
            <div className={level <= index ? " level-on" : "level-off"}></div>
        )
    }
    return (
        <div className={className}>
            <img className="main-skill-icon" src={"./Icons/" + StackIconHandler.GetIcon(name)} alt=""/>
            <div className="main-skill-level">
                <div className="main-skill-exp">experience: {years} years</div>
                <div className="level-meter">
                    {skillLevelDots}
                </div>
            </div>
        </div>
    )
}


export function OtherSkill({name, className}) {
    return (
        <div className={className}>
            <div className={"minor-skill-container"}>
                <img src={"./Icons/" + StackIconHandler.GetIcon(name)} alt={name.toLowerCase()} className="minor-skill"/>
            </div>
        </div>
    )
}
