const TechStackIcons = {
    "csharp" : "csharp.png",
    "unity" : "unity.webp",
    "android" : "android.webp",
    "dotnet" : "dotnet.png",
    "javascript" : "javascript.png",
    "sass" : "sass.png",
    "aws" : "aws.png",
    "firebase" : "firebase.png",
    "react" : "react.png",
    "azure" : "azure.png",
    "hlsl" : "hlsl.png",
    "jenkins" : "jenkins.png",
    "mssql" : "mssql.png"
}

export class StackIconHandler {
    static GetIcon(stack) {
        let icon = TechStackIcons[stack.toLowerCase()];
        if(icon != null)
         return icon;
        return stack.toLowerCase() + ".png";
    }
}