import React from 'react'
import './Experience.scss';

export default function ExperienceJobTitle(props) {
    props = props.experience;
    return (
        <div className="experience--container experience-job-title-component">
            <img className="img--center experience--logo" src={'./Icons/'+props.logo} alt={props.title}></img>
            <div className="experience--role text--center">{props.position}</div>
            <div className="experience--time">{props.workFrom + " - " + props.workTo}</div>
        </div>
    )
}

